import React, { useEffect, useMemo, useRef, useState } from 'react'
import { Ido } from 'state/types'

import { Route, useRouteMatch } from 'react-router-dom'
import { Heading, Flex, Image, Text } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'
import FlexLayout from 'components/layout/Flex'
import Page from 'components/layout/Page'
import PageHeader from 'components/PageHeader'
import PoolCard from './components/PoolCard'
import PoolTabButtons from './components/PoolTabButtons'
import BountyCard from './components/BountyCard'
import _idoData from './idosData.json'

const Idos: React.FC = () => {
  const { path } = useRouteMatch()
  const { t } = useTranslation()
  const idoData: Ido[] = _idoData as any
  const listUpcoming = () => idoData.filter(ido => ido.status === 'upcoming');
  const listActive = () => idoData.filter(ido => ido.status === 'active');
  const listClosed = () => idoData.filter(ido => ido.status === 'closed');
  return (
    <>
      <PageHeader>
        <Flex justifyContent="space-between" flexDirection={['column', null, 'row']}>
          <Flex flexDirection="column" mr={['8px', 0]}>
            <Heading as="h1" scale="xxl" color="secondary" mb="24px">
              {t('IMO Pools')}
            </Heading>
            <Heading scale="md" color="text">
              {t('List IMOs on GaurStarter')}
            </Heading>
            <Heading scale="md" color="text">
              {t('Innovation Project, High APR ever')}
            </Heading>
          </Flex>
        </Flex>
      </PageHeader>
      <Page>
        <PoolTabButtons />
        <FlexLayout>
          <Route exact path={`${path}`}>
            {listUpcoming().length>0?listUpcoming().map(project => <PoolCard project={project} />):(<Text>No upcoming IMO data</Text>)}
          </Route>
          <Route path={`${path}/active`}>
            {listActive().length>0?listActive().map(project => <PoolCard project={project} />):(<Text>No active IMO data</Text>)}
          </Route>
          <Route path={`${path}/finished`}>
            {listClosed().length>0?listClosed().map(project => <PoolCard project={project} />):(<Text>No finished IMO data</Text>)}
          </Route>
        </FlexLayout>
      </Page>
    </>
  )
}

export default Idos
