import BigNumber from 'bignumber.js'
import Countdown from "react-countdown";
import React from 'react'
import { CardBody, Flex, Text, CardRibbon, Progress, Button, Tag } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'
import { BIG_ZERO } from 'utils/bigNumber'
import { Ido } from 'state/types'
import AprRow from './AprRow'
import { StyledCard, StyledCardInner } from './StyledCard'
import CardFooter from './CardFooter'
import StyledCardHeader from './StyledCardHeader'
import CardActions from './CardActions'

const PoolCard: React.FC<{ project: Ido }> = ({ project }) => {
  const { t } = useTranslation()

  const isFinished = project.status === 'closed'
  const finishedRibbon = isFinished && <CardRibbon variantColor="textDisabled" text={t('Finished')} />
  const isUpcoming = project.status === 'upcoming'
  const upcomingRibbon = isUpcoming && <CardRibbon variantColor="success" text={t(project.idoDate)} />

  return (
    <StyledCard
      isFinished={isFinished}
      ribbon={finishedRibbon || upcomingRibbon}
    >
      <StyledCardInner>
        <StyledCardHeader project={project} />
        <CardBody>
          <Flex mt="5px" flexDirection="column">
            {isUpcoming && <Tag variant='primary' outline>
              <Text>{t('Start in: ')}</Text>
              <Text ml="10px" color="secondary">
                <Countdown date={new Date(project.idoDate)} />
              </Text>
            </Tag>}
            {isFinished && <Tag variant='secondary' outline>
              <Text>{`${t('End at: ')} ${(new Date(project.endDate)).toLocaleString()}`}</Text>
            </Tag>}
            {(!isUpcoming && !isFinished) && <Button variant='secondary'>
              <Flex flexDirection="column">
                <Text>{`${t('Start at: ')} ${(new Date(project.idoDate)).toLocaleString()}`}</Text>
                <Text>{t('End in: ')} <Countdown date={new Date((new Date(project.idoDate)).getTime() + 1000 * project.durationMinute * 60)} /></Text> 
              </Flex>
            </Button>}
            <Text mt="10px" mb="10px" fontSize="12px" color="textSubtle">
              {t(project.projectDescription)}
            </Text>
            <Text>{t('Progress')}</Text><Progress showProgressBunny primaryStep={project.raisedAmount * 100 / project.totalFund} />
            <Flex mt="10px" justifyContent="space-between">
              <Button variant="secondary">
                <Flex flexDirection="column">
                  <Text fontSize="8px">{t('Base coin')}</Text>
                  <Text>
                    {t(project.baseCoin)}</Text>
                </Flex>
              </Button>
              <Button variant="secondary">
                <Flex flexDirection="column">
                  <Text fontSize="8px">{t(`1${project.baseCoin} rate`)}</Text>
                  <Text>
                    {t(project.rate.toString())}</Text>
                </Flex>
              </Button>
              <Button variant="secondary">
                <Flex flexDirection="column">
                  <Text fontSize="8px">{t('Access')}</Text>
                  <Text>
                    {t('Private')}</Text>
                </Flex>
              </Button>
            </Flex>
          </Flex>
        </CardBody>
        <CardFooter project={project} />

      </StyledCardInner >
    </StyledCard >
  )
}

export default PoolCard
