import React from 'react'
import styled from 'styled-components'
import { useRouteMatch, Link, useLocation } from 'react-router-dom'
import {
  ButtonMenu,
  ButtonMenuItem,
  Button,
  HelpIcon,
  Toggle,
  Text,
  Flex,
  NotificationDot,
  Link as UiKitLink,
} from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'

const ButtonText = styled(Text)`
  display: none;
  ${({ theme }) => theme.mediaQueries.lg} {
    display: block;
  }
`

const StyledLink = styled(UiKitLink)`
  width: 100%;

  &:hover {
    text-decoration: none;
  }
`

const PoolTabButtons = () => {
  const { url, isExact } = useRouteMatch()
  const { pathname } = useLocation();
  const { t } = useTranslation();
  const getIndex = () => {
    if (isExact) return 0
    if (pathname === `${url}/active`) return 1;
    return 2
  }

  return (
    <Flex alignItems="center" justifyContent="center" mb="32px">
      <Flex alignItems="center" flexDirection={['column', null, 'row', null]}>
        <ButtonMenu activeIndex={getIndex()} scale="sm" variant="subtle">
          <NotificationDot show>
            <ButtonMenuItem as={Link} to={`${url}`}>
              {t('Upcoming')}
            </ButtonMenuItem>
          </NotificationDot>

          <ButtonMenuItem as={Link} to={`${url}/active`}>
            {t('Live')}
          </ButtonMenuItem>
          <ButtonMenuItem as={Link} to={`${url}/finished`}>
            {t('Finished')}
          </ButtonMenuItem>
        </ButtonMenu>
      </Flex>
    </Flex>
  )
}

export default PoolTabButtons
