import React, { useState } from 'react'
import BigNumber from 'bignumber.js'
import styled from 'styled-components'
import { useTranslation } from 'contexts/Localization'
import { useWeb3React } from '@web3-react/core'
import { Flex, CardFooter, ExpandableLabel, HelpIcon, Button, Text } from '@pancakeswap/uikit'
import { Ido } from 'state/types'
import UnlockButton from 'components/UnlockButton'
import useToast from 'hooks/useToast'

const ExpandableButtonWrapper = styled(Flex)`
  align-items: center;
  justify-content: space-between;
  button {
    padding: 0;
  }
`
const Footer: React.FC<{ project: Ido }> = ({ project }) => {
  const { t } = useTranslation()
  const [isExpanded, setIsExpanded] = useState(false)
  const { account } = useWeb3React()
  const buttonHandler=()=>toastError(t('Error'), t('Under constructing!'))
  const buyAction = project.status==="active" && <Button onClick={buttonHandler}>Buy IDO</Button>
  const claimAction = project.status==="closed" && <Button onClick={buttonHandler}>Claim your token</Button>
  const joinWhiteListAction = project.status==="upcoming" && <Button onClick={buttonHandler}>Join whitelist</Button>
  const { toastSuccess, toastError } = useToast()
  return (
    <CardFooter>
      <ExpandableButtonWrapper>
        <Flex alignItems="center">
          <Flex >
            <HelpIcon ml="4px" width="20px" height="20px" color="textSubtle" />
          </Flex>
        </Flex>
        <ExpandableLabel expanded={isExpanded} onClick={() => setIsExpanded(!isExpanded)}>
          {isExpanded ? t('Hide') : t('Details')}
        </ExpandableLabel>
      </ExpandableButtonWrapper>
      {isExpanded && <>
        <Flex flexDirection="column" justifyContent="center">
          <Text>{t('Token name: ') + project.tokenName}</Text>
          {account ? (buyAction || joinWhiteListAction || claimAction) : (<UnlockButton />)}

        </Flex>

      </>}
    </CardFooter>
  )
}

export default Footer
